<!-- 地址选择组件 -->
<template>
  <Form :model="form" class="demo-ruleForm" :rules="rules" ref="form" label-position="top">

  <Row :gutter="20" class="address">
      <Col :span="5">
        <Form-item prop="province">
        <Select v-model="form.province" placeholder="请输入省" @change="proChange" >
          <Option
            v-for="item in provinces"
            :key="item.value"
            :value="item.value">
          </Option>
        </Select>
        </Form-item>
      </Col>

    <Col :span="5">
      <Form-item  prop="city">

      <Select v-model="form.city" placeholder="请输入市" @change="cityChange" >
        <Option
          v-for="item in citys"
          :key="item.value"
          :value="item.value">
        </Option>
      </Select>
      </Form-item>

    </Col>

    <Col :span="12">
      <Form-item  prop="detail">

      <Input
        placeholder="请填写详细地址"
        :number="true"
        v-model="form.detail">
      </Input>
      </Form-item>

    </Col>

  </Row>
</Form>
</template>
<script>
  import addressData from '@/assets/js/addressData.js'
  import store from '@/store'

  function formatData(data){
    var result = [];
    for(var key in data){
      result.push({
        value: key
      })
    }
    return result
  }

  export default {
    name:'address',
    props:['province','city','detail','isAddressTrue'],
    data: function () {
      return {
        rules:{
          province:[{required:true,message:'请选择省份',trigger:'change'}],
          city:[{required:true,message:'请选择城市',trigger:'change'}],
          detail:[{required:true,message:'请填写详细地址',trigger:'change'}]
        },
        form:{
          province: this.province,
          city: this.city,
          detail: this.detail
        },
        provinces: formatData(addressData)
      }
    },
    watch: {
      form: {
        handler:function(){
          store.commit('setRuleForm',this.form);
        },
        deep:true
      },
      ifCheckFrom: function (val,oldVal) {
        if(val){
          this.$refs.form.validate((valid) => {
          }) ;
        }
      }
    },
    computed: {
      citys: function (){
        return formatData(addressData[this.form.province])
      },
      ifCheckFrom: function () {
        return this.isAddressTrue
      }
    },
    methods: {
      proChange: function (val,oldVal) {
        if(oldVal){
          this.form.city='';
          this.form.detail='';
        }
        var data = formatData(addressData[this.form.province]);
        for(var i =0; i<data.length; i++){
          this.$set(this.citys,i,data[i]);
        }
      },
      cityChange: function (val, oldVal) {
        if (oldVal) {
          this.form.detail = '';
        }
      },
    }
  }
</script>
<style>

  .address .iview-form-item{margin-bottom: 0!important;margin-right: 0!important;}
</style>



